html, body {
  height: 100%;
  margin: 0;
  background-color: #f5f5f5;
  background-image: url('https://aipict.com/wp-content/uploads/2023/09/snowy-mountains01.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  font-family: 'Arial', sans-serif;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

#root {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  height: 100vh;
  width: 100%;
}

.title-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 70px; /* Fixed height for the title bar */
  background-color: rgba(255, 255, 255, 0.718);
  padding: 10px 0;
  box-sizing: border-box;
}

.title {
  font-size: 1.5rem;
  color: #333;
  font-weight: bold;
  margin: 0 20px;
}

.game-container {
  flex: 1; /* Use the remaining space */
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  border: none;
  background-color: transparent;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center; /* Center elements vertically in the game container */
}

.snowboarder {
  position: absolute;
  top: 50%;
  left: 25%;
  transform: translate(-50%, -50%);
  width: 192.3px;
  height: 192.3px;
  background-size: contain;
  background-repeat: no-repeat;
}

.bigfoot {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 192.3px;
  height: 192.3px;
  background-size: contain;
  background-repeat: no-repeat;
}

.score {
  position: absolute;
  top: 0;
  font-size: 60px;
  font-weight: bold;
  color: #333;
  text-align: center;
  width: 100%;
}

input[type='file'],
.prompt-input {
  margin: 20px 0;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: #fff;
  font-size: 1rem;
  width: 80%;
  max-width: 400px;
}

.upload-button {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
  font-size: 1rem;
}

.upload-button:hover {
  background-color: #0056b3;
}


.response-box {
  margin-top: 20px;
  background-color: #fff;
  color: #333;
  padding: 15px;
  border-radius: 8px;
  width: 80%;
  max-width: 400px;
  min-height: 50px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border: 1px solid #ddd;
  overflow-wrap: break-word;
  white-space: pre-wrap;
  text-align: left;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.response-box.scrollable {
  max-height: 200px; /* Set a maximum height */
  overflow-y: auto;  /* Enable vertical scrolling if the content exceeds max-height */
}

.loading-gif {
  width: 400px;
  height: 400px;
}

.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  height: calc(100vh - 90px);
  padding-top: 120px;
  text-align: center;
}

.home-title {
  font-size: 3rem;
  color: #333;
  margin: 5px 0;
}

.home-description {
  font-size: 1.8rem;
  color: #666;
  margin-top: 10px;
}